
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Display", sans-serif;
}

body {
  background: #fafafa;
  color: #222;
}


.navbar {
  width: 100%;
  background: #ffffff;
  padding: 18px 40px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-logo {
  font-size: 24px;
  font-weight: 700;
  color: #6a1b9a;
}

.nav-links a {
  margin-left: 30px;
  text-decoration: none;
  color: #444;
  font-weight: 500;
  transition: 0.2s;
}

.nav-links a:hover {
  color: #6a1b9a;
}

/* ----------- FOOTER ----------- */
.footer {
  margin-top: 80px;
  padding: 30px;
  background: #ffffff;
  text-align: center;
  font-size: 14px;
  color: #777;
  border-top: 1px solid #e5e5e5;
}

/* ----------- HOME PAGE ----------- */
.home-container {
  padding: 120px 40px;
  text-align: center;
}

.home-title {
  font-size: 58px;
  font-weight: 700;
  margin-bottom: 20px;
}

.home-subtitle {
  font-size: 20px;
  color: #555;
  margin-bottom: 40px;
}

.primary-btn {
  background: #7b1fa2;
  color: white;
  padding: 14px 32px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: 0.2s;
}

.primary-btn:hover {
  background: #6a1b9a;
}

/* ----------- PAGE LAYOUT ----------- */
.page-container {
  max-width: 900px;
  margin: 80px auto;
  padding: 20px;
}

/* ----------- UPLOAD BOX ----------- */
.upload-box {
  background: white;
  padding: 40px;
  border-radius: 16px;
  border: 2px dashed #c69ee6;
  text-align: center;
  cursor: pointer;
  transition: 0.2s;
}

.upload-box:hover {
  border-color: #7b1fa2;
}

/* ----------- RESULT BOX ----------- */
.result-box {
  background: white;
  padding: 30px;
  border-radius: 16px;
  margin-top: 30px;
  border: 1px solid #eee;
}

.score {
  font-size: 32px;
  color: #7b1fa2;
  font-weight: bold;
  margin-bottom: 20px;
}

.download-btn {
  margin-top: 20px;
  padding: 12px 20px;
  background: #7b1fa2;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
